.main-nav__list {
  display: none;
  flex-direction: column;

  @include list-style-none;

  @include min(768) {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
}

.main-nav__item {
  width: 100%;
  background-color: $lightblue;

  @include max(767) {
    border-top: 1px solid rgba(255, 255, 255, 0.2);
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    color: $white;

    &:last-child {
      position: relative;
      border-bottom: none;

      &::before {
        content: "";
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 7px;
        background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
      }
    }

    &:hover {
      background-color: $blue;
    }

    &:active {
      background-color: $blue-2;
      color: rgba(255, 255, 255, 0.3);
    }
  }

  @include min(768) {
    background-color: $white;
    width: 20%;
    border: none;
  }

  &:nth-child(2),
  &:nth-child(6),
  &:nth-child(10) {
    @include min(768) {
      margin-right: 20%;
    }
  }
}

.main-nav__link {
  display: block;
  padding: 10px 0 5px;
  color: inherit;

  @include text-parametr(18px, 40px, 700);
  text-decoration: none;

  @include min(768) {
    color: $font-color;
    padding: 0;

    @include text-parametr(14px, 24px, 700);

    &:hover {
      color: $lightblue;
    }

    &:active {
      color: rgba(0, 0, 0, 0.3);
    }

    &--active {
      color: $brown;

      &:hover {
        color: $darkbrown;
      }

      &:active {
        color: rgba(118, 99, 87, 0.3);
      }
    }
  }
}

.main-nav__toggle {
  position: absolute;
  top: 17px;
  right: 20px;
  z-index: 2;
  outline: none;
  border: none;
  width: 23px;
  height: 22px;
  background: url("../img/icon-menu.svg") 0 0 no-repeat;

  &--opened {
    height: 23px;
    background: url("../img/icon-close.svg") 0 0 no-repeat;
  }

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }

  @include min(768) {
    display: none;
  }
}

.main-nav {
  position: relative;
  z-index: 1;

  @include max(767) {
    &--opened {
      .main-nav__list {
        display: block;
      }
    }

    &--nojs {
      .main-nav__list {
        display: block;
      }

      .main-nav__toggle {
        display: none;
      }
    }
  }
}
